.baloon-container {
  position:relative;
  padding:0px;
  border:0px;
  background:transparent;
  /* we have to set size of the container for the heart */
  width:150px;
  height:50px;
  top:0px;
  left:0px;
  /*border:1px solid green;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index:1000;

}
.baloon-container > .baloon {
  position:absolute;
  overflow:visible;
  width:150px;
  height:100px;
  /*border:1px solid red;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.baloon-container > .baloon.flyAway {
  -webkit-transition: all 7s;
  -o-transition: all 7s;
  transition: all 7s;
}

.baloon-container > .baloon > .heart-container {
  position:relative;
  width:100%;
  height:100%;
  top:0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.baloon-container > .baloon > .heart-container > .heart {
  position: relative;
  border-radius: 20px;
  border: 3px solid #dd7375;
  font-size: 27px;
  padding: 10px 0px;
  color: #dd7375;
  z-index: 100;
  background-color: #ffffff;
  cursor: pointer;
  margin: auto;
  width: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  top: 25px;
  height: 40px;
  line-height: 14px;
}
.baloon-container > .baloon > .heart-container > .heart:after {
  content:"";
  background-color:#AAA;
  width:10px;
  height:5px;
  border-radius:5px;
  position:absolute;
  bottom:-37px;
  left:50%;
  margin-left:-5px;
}
.baloon-container > .baloon > .heart-container > .heart > .leftTop,
.baloon-container > .baloon > .heart-container > .heart > .rightTop {
  position:absolute;
  background:#dd7375;
  color:#dd7375;
  height: 33px;
  width: 70px;
  border-top-left-radius: 70px;
  border-top-right-radius: 70px;
  top:-35px;
}
.baloon-container > .baloon > .heart-container > .heart > .leftTop {
  left:10px;
}
.baloon-container > .baloon > .heart-container > .heart > .rightTop {
  right:10px;
}
.baloon-container > .baloon > .heart-container > .heart > .bottomMiddle {
  position:absolute;
  left:50%;
  margin-left:-38px;
  height:0px;
  width:0px;
  bottom:-33px;
  border-top:30px solid #dd7375;
  border-bottom:none;
  border-left:38px solid transparent;
  border-right:38px solid transparent;
}
.baloon-container > .baloon > .heart-container > .heart > .bottomMiddle:after {
  border:10px solid #dd7375;
  border-top:none;
  content:"";
  position:absolute;
  border-left-color:transparent;
  border-right-color:transparent;
  margin-left:-10px;
  left:50%;
}

.baloon-container > .line {
  position:absolute;
  width:2px;
  margin-top:-1px;
  background-color:#DDD;
  z-index:1000;

}
/*
.baloon-container > .baloon,
.baloon-container > .line {
    transition: all .1s !important;
}
*/
/* this is just a point for calculation of the line position,
   keep size 1px x 1px for easier calculation
   real kid will be inside called from KID component */
.baloon-container > .kid-placeholder {
  width:1px; /* keep 1px */
  height:1px; /* keep 1px */
  border-radius:50%;
  background:red;
  position:absolute;
  top:300px;
  left:200px;
  cursor:pointer;
}



