@media screen and (max-width: 980px) {

    .detail {
        position: fixed;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        margin: 0px;
        z-index: 990;
        background: white;
        display: none;
    }

    .detail.active {
        display: block;
    }

    .detail.active .back-button {
        display: block;
        position: fixed;
    }

    .detail > .general {
        padding: 10px;
    }

    .detail > .general > .title {
        font-size: 30px;
        margin-bottom: 0px;
    }

    .detail > .general > .subtitle {
        font-size: 20px;
    }

    .detail > .content {
        margin-bottom: 50px;
    }

    .detail > .content > .buttons {
        position:fixed;
        bottom: 10px;
        right: 10px;
    }

    .detail > .content > .buttons > .button.button-next {
        display: block;
        -webkit-box-shadow: 3px 3px 10px #acb2c6;
                box-shadow: 3px 3px 10px #acb2c6;
    }

    .detail > .content > .bottom-photo {
        display: none;
    }

    .detail > .content > .description {
        padding: 0px 20px;
    }

    .detail > .content > .items > .item {
        border: 0px dashed transparent !important;
        position: relative;
        padding: 0px;
    }

    .detail > .content > .items > .item > .myHead > .name {
        font-size: x-large;
    }

    .detail > .content > .items > .item > .share-component {
        left: -30px;
    }

    .detail > .content > .items > .item > .logos {
        display: none;
    }
}

@media screen and (max-width: 500px) {
    .detail.active {

    }

    .detail > .general > .subtitle {
        display: none;
    }

    .detail.active .back-button {
        right: -5px;
        top: -5px;
        font-size: 30px;
    }

    .detail .back-button:hover:after {
        top: -90px;
    }

    .detail > .content {
        padding: 10px;
    }

    .detail > .content > .description {
        padding: 0px 0px;
    }

    .detail > .content > .photo {
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .detail > .content > .items {
        padding: 0px;

    }

    .detail > .content > .items > .item {
        padding-bottom: 30px;
        margin-bottom: 30px;
    }

    .detail > .content > .items > .item > .myHead > .name {
        font-size: 20px;
    }

    .detail > .content > .items > .item > .logos {
        position: initial;
        margin-bottom: 10px;
        margin-top: -10px;
    }

    .detail > .content > .items > .item > .logos > img {

    }

    .detail > .content > .items > .item > .logos:empty {
        display: none;
    }

    .detail > .content > .items > .item > .myHead > .subname {
        font-size: 16px;
    }

    .detail > .content > .items > .item > .timerange {
        text-align: right;
        font-weight: normal;
        font-size: 16px;
    }

    .detail > .content > .items > .item > .timerange + .timerange {
        right: 0px;
    }

    .detail > .content > .items > .item > .myHead {

    }

    .detail > .content > .bulletgroups > .title {
        text-align: center;
    }

    .detail > .content > .bulletgroups > .groups > .group {
        padding: 0px;
    }

    .detail > .content > .bulletgroups > .groups > .group > .subtitle {
        text-align: center;
    }

    .detail > .content > .bulletgroups > .groups > .group > .description {
        text-align: center;
        margin-bottom: 0px;
        display: none;
    }

    .detail > .content > .bulletgroups > .groups > .group > .bullets {
        margin-top: 0px;
    }

    .detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet {
        text-align: center;
        margin-bottom: 5px;
        padding: 0px 0px;
        background: #fbfbfb;
    }

    .detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .bubbles {
        margin-bottom: 40px;
        background: white;
    }

    .detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .label {
        display: block;
        padding-top: 10px;
        position: relative;
    }

    .detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .label > .my-photo {
        display: block;
        margin: auto;
        margin-bottom: 10px;
        position: initial;
    }

    .detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .label > .icon {
        display: block;
        margin: auto;
        margin-bottom: 10px;
        position: initial;
    }

    .detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .label > .icon:after {

    }

    .detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .value {
        display: block;
        padding-bottom: 10px;

    }
}



