.detail {
    vertical-align: top;
    border: 1px solid #DDD;
    -webkit-box-shadow: 2px 2px 3px #CCC;
            box-shadow: 2px 2px 3px #CCC;
    min-height: 600px;
    margin-bottom: 10px;
    position: relative;
    overflow: hidden;
    background: white;
}

.detail .back-button {
    position: absolute;
    top: 0px;
    right: 0px;
    font-size: 50px;
    margin: 10px;
    cursor: pointer;
    z-index: 1000;
    display: none;
}

.detail .back-button:after {
    content: "Close detail";
    display: block;
    position: absolute;
    font-size: 15px;
    white-space: nowrap;
    right: 30px;
    padding: 5px;
    padding-top: 100px;
    background: white;
    top: -150px;
    color: red;
    -webkit-transition: top .4s;
    -o-transition: top .4s;
    transition: top .4s;
}

.detail > .general {
    padding: 20px;
    position: relative;
}

.detail > .general > .title {
    font-size: 40px;
    color: black;
    margin-bottom: 10px;
}

.detail > .general > .title > .fa {
    color: black;
    text-shadow: 1px 1px 0px white;
    margin-right: 10px;
    position: absolute;
    right: 0px;
}

.detail > .general > .subtitle {
    font-size: 25px;
    color: #4885ed;
}

.detail > .general > .description {
    font-size: 20px;
    color: #4885ed;
}

.detail > .content {
    padding: 30px 0px;
    background: white;
}

.detail > .content > .buttons {
    text-align: right;
}

.detail > .content > .buttons > .button {
    text-align: center;
    color: #4d88ed;
    display: inline-block;
    text-decoration: none;
    border: 1px solid #DDD;
    width: auto;
    padding: 6px;
    background: #fafafa;
}

.detail > .content > .buttons > .button.button-next {
    display:none;
}

.detail > .content > .photo {
    text-align: center;
    position: relative;
}

.detail > .content > .photo:after {
    left: initial;
    right: 100px;
}

.detail > .content > .bottom-photo {
    text-align: center;
    max-height: 200px;
    position: relative;
    bottom: 0px;
    float: right;
}

.detail > .content > .photo > img {
    height: 120px;
    width: 120px;
    border-radius: 50%;
    border-radius: 50%;
    overflow: hidden;
}

.detail > .content > .description {
    text-align: justify;
    display: inline-block;
    padding: 0px 60px;
}

.detail > .content > .description:empty {
    display: none;
}

.detail > .content > .description > p:first-child {
    /*text-indent: 50px;*/
}

.detail > .content > .description > p {

}

.detail > .content > .items {
    padding: 20px;
}

.detail > .content > .items:empty {
    display: none;
}

.detail > .content > .items > .item {
    margin-bottom: 50px;
    padding: 20px 20px 20px 20px;
    position: relative;
    -webkit-transition: outline-color .5s;
    -o-transition: outline-color .5s;
    transition: outline-color .5s;
    outline-color: transparent;
    border: 20px dashed transparent;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}

.detail > .content > .items > .item.funny {
    background: #fffdf7;
    -webkit-box-shadow: 3px 3px 13px #d2d2d2;
            box-shadow: 3px 3px 13px #d2d2d2;
    padding: 15px;
    margin-left: -15px;
    margin-right: -15px;
}

.detail > .content > .items > .item:not(:last-child)::after {
    content: "";
    height: 1px;
    width: 100%;
    background: #DDD;
    display: block;

}

.detail > .content > .items > .item > .timerange {
    font-weight: bold;
    font-size: 20px;
    /* color: #4885ed; */
    /* border-bottom: 1px solid #DDD; */
    /* text-align: right; */
}

.detail > .content > .items > .item > .timerange + .timerange {
    position: absolute;
    top: -17px;
}

.detail > .content > .items > .item > .timerange > .date {

}

.detail > .content > .items > .item > .timerange > .from {
    display: inline-block;
}

.detail > .content > .items > .item > .timerange > .to:before {
    content: "-";
    padding: 0px 5px;
}

.detail > .content > .items > .item > .timerange > .to {
    display: inline-block;
}

.detail > .content > .items > .item.funny .from,
.detail > .content > .items > .item.funny .to {
    display: none;
}

.detail > .content > .items > .item > .myHead {
    text-align: right;
    margin-bottom: 10px;

}

.detail > .content > .items > .item > .myHead > .name {
    font-size: larger;
    font-size: xx-large;
}

.detail > .content > .items > .item.currently-visible {
    border-color: #f8f8f8;
}

.detail > .content > .items > .item.currently-visible > .myHead > .name {
    color: red !important;
}

.detail > .content > .items > .item > .myHead > .subname {
    font-size: 20px;
}

.detail > .content > .items > .item .url-buttons {
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
}

.detail > .content > .items > .item .url-buttons > .share-component,
.detail > .content > .items > .item .url-buttons > .www,
.detail > .content > .items > .item .url-buttons > .github {
    color: #4d88ed;
    display: inline-block;
    text-decoration: none;
    border: 1px solid #DDD;
    width: auto;
    padding: 6px;
    background: #fafafa;
    color: #777;
}

.detail > .content > .items > .item .url-buttons > .share-component {
    color: black;
    background: white;
}

.detail > .content > .items > .item .url-buttons > .www {
    color: black;
    background: white;
}

.detail > .content > .items > .item .url-buttons > .github {
    color: black;
    background: white;
}

.detail > .content > .items > .item .url-buttons > .share-component + *,
.detail > .content > .items > .item .url-buttons > .www + * {
    margin-left: 5px;
}

.detail > .content > .items > .item .url-buttons > .share-component > .fa,
.detail > .content > .items > .item .url-buttons > .www > .fa,
.detail > .content > .items > .item .url-buttons > .github > .fa {
    margin-right: 5px;
}

.detail > .content > .items > .item .url-buttons > .share-component:hover,
.detail > .content > .items > .item .url-buttons > .www:hover,
.detail > .content > .items > .item .url-buttons > .github:hover {
    color: red;
}

.detail > .content > .items > .item > .logos {
    position: absolute;
    left: 15px;
    top: 60px;
}

.detail > .content > .items > .item > .logos > img {
    height: 50px;
    padding-right: 10px;
}

.detail > .content > .items > .item > .myBody {
    position: relative;
}

.detail > .content > .items > .item > .myBody > .item-image {
    max-width: 460px;
    max-height: 320px;
    border-radius: 0px;
    overflow: hidden;
    margin: 40px auto 0px auto;
    -webkit-box-shadow: 3px 3px 3px #DDD;
    box-shadow: 10px 10px 20px #a7a7a7;
}

.detail > .content > .items > .item > .myBody > .item-image > img {
    min-height: 100%;
    margin: auto;
    -webkit-transform: rotate(-3deg);
        -ms-transform: rotate(-3deg);
            transform: rotate(-3deg);
    position: relative;
    top: -20px;
    left: -20px;
    max-width: 140%;
}

.detail > .content > .items > .item > .myBody > .description {
    text-align: justify;
    /*text-indent: 50px;*/
}

.detail > .content > .items > .item > .myBody > ul.simple-list {

}

.detail > .content > .items > .item > .myBody > ul.simple-list > li {
    list-style-type: none;
}

.detail > .content > .items > .item > .myBody > ul.simple-list > li > a,
.detail > .content > .items > .item > .myBody > ul.simple-list > li > a:hover,
.detail > .content > .items > .item > .myBody > ul.simple-list > li > a:active {
    color: #484848;
    text-decoration: none;
    cursor: pointer;
}

.detail > .content > .items > .item > .myBody > ul.simple-list > li > a:hover {
    text-decoration: underline;
    color: black;
}

.detail > .content > .items > .item > .myBody > ul.simple-list > li > a > .fa {
    margin-right: 10px;
    font-size: larger;
    color: #2fa746;
}

.detail > .content > .items > .item > .myBody > ul.simple-list > li > a > .name {

}

.detail > .content > .items > .item > .myBody > .notes {
    margin-top: 10px;
    color: #555;
    font-style: italic;
}

.detail > .content > .bulletgroups {
    /*padding-bottom:30px;
    border:1px solid #EEE;*/
    border-bottom: 1px solid #ececec;
    padding-bottom: 20px;
    margin-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
}

.detail > .content > .bulletgroups:last-child {
    border-bottom: none;
    padding-bottom: 20px;
    margin-bottom: 0px;
}

.detail > .content > .bulletgroups:empty {
    display: none;
}

.detail > .content > .bulletgroups > .title {
    padding: 0px 20px;
    font-weight: bold;
    font-size: 30px;
}

.detail > .content > .bulletgroups > .title:empty {
    display: none;
}

.detail > .content > .bulletgroups > .groups {

}

.detail > .content > .bulletgroups > .groups > .group {
    padding: 20px 30px;
    margin-bottom: 20px;
}

.detail > .content > .bulletgroups > .groups > .group:last-child {
    border-bottom: none;
    margin-bottom: 0px;
}

.detail > .content > .bulletgroups > .groups > .group > .subtitle {
    font-weight: bold;
    font-size: 20px;
}

.detail > .content > .bulletgroups > .groups > .group > .description {
    font-weight: bold;
    font-size: 15px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.detail > .content > .bulletgroups > .groups > .group > .description:empty {
    display: none;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets {
    margin-top: 0px;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet {
    margin-bottom: 10px;
    display: block;
    text-decoration: none;
    color: black;
    cursor: default;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet.can-click {
    cursor: pointer;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet.can-click:hover {
    color: rgb(247, 11, 11);
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet.can-click > .label {
    cursor: pointer;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .label {
    font-weight: bold;
    margin-right: 5px;
    display: inline;
    white-space: nowrap;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .label > .icon {
    margin-right: 10px;
    display: inline;
    font-size: 30px;
    width: 45px;
    display: inline-block;
    text-align: center;
    position: relative;
    top: 3px;
    -webkit-filter: grayscale(10%); /* Safari 6.0 - 9.0 */
    filter: grayscale(10%);
    opacity: .7;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet:nth-child(1) > .label > .icon {
    color: #9400D3;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet:nth-child(2) > .label > .icon {
    color: #4B0082;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet:nth-child(3) > .label > .icon {
    color: #0000FF;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet:nth-child(4) > .label > .icon {
    color: #00e000;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet:nth-child(5) > .label > .icon {
    color: #d0d000;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet:nth-child(6) > .label > .icon {
    color: #FF7F00;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet:nth-child(7) > .label > .icon {
    color: #FF0000;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .label > img {
    height: 30px;
    margin-right: 10px;
    position: relative;
    top: 10px;
    border: 1px solid #DDD;
    border-radius: 4px;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .value {
    font-weight: normal;
    display: inline;
    white-space: nowrap;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .bubbles {
    text-align: center;
    margin-left: -20px;
    margin-right: -20px;
    position: relative;
    margin-top: 10px;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .bubbles > .bubble {
    border: 1px solid #DDD;
    margin: -5px;
    padding: 10px;
    display: inline-block;
    border-radius: 40px;
    background: #f9f9f9;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .bubbles > .bubble.no-style {
    padding: 0px !important;
    border: none !important;
    border-radius: 0px !important;
    display: inline-block !important;
    background: transparent !important;
    position: relative;
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .bubbles > .bubble:hover {

}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .bubbles > .bubble.size-1 {
    border: 5px solid #DDD;
    font-size: 35px;
    padding: 15px;
    z-index: 100;
    -ms-transform: rotate(-7deg); /* IE 9 */
    -webkit-transform: rotate(-7deg); /* Safari 3-8 */
    transform: rotate(-7deg);
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .bubbles > .bubble.size-2 {
    border: 4px solid #DDD;
    font-size: 30px;
    padding: 13px;
    z-index: 99;
    -ms-transform: rotate(5deg); /* IE 9 */
    -webkit-transform: rotate(5deg); /* Safari 3-8 */
    transform: rotate(5deg);
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .bubbles > .bubble.size-3 {
    border: 3px solid #DDD;
    font-size: 25px;
    padding: 11px;
    z-index: 98;
    -ms-transform: rotate(-5deg); /* IE 9 */
    -webkit-transform: rotate(-5deg); /* Safari 3-8 */
    transform: rotate(-5deg);
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .bubbles > .bubble.size-4 {
    border: 2px solid #DDD;
    font-size: 20px;
    padding: 9px;
    z-index: 97;
    -ms-transform: rotate(-3deg); /* IE 9 */
    -webkit-transform: rotate(-3deg); /* Safari 3-8 */
    transform: rotate(-3deg);
}

.detail > .content > .bulletgroups > .groups > .group > .bullets > .bullet > .bubbles > .bubble.size-5 {
    border: 1px solid #DDD;
    font-size: 15px;
    padding: 7px;
    z-index: 96;
    -ms-transform: rotate(7deg); /* IE 9 */
    -webkit-transform: rotate(7deg); /* Safari 3-8 */
    transform: rotate(7deg);
}



